
.sketch-color-picker--c-hue {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-radius: 2px;
}
.sketch-color-picker--c-hue--horizontal {
    background: -webkit-gradient(linear, left top, right top, from(#f00), color-stop(17%, #ff0), color-stop(33%, #0f0), color-stop(50%, #0ff), color-stop(67%, #00f), color-stop(83%, #f0f), to(#f00));
    background: linear-gradient(to right, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
}
.sketch-color-picker--c-hue--vertical {
    background: -webkit-gradient(linear, left bottom, left top, from(#f00), color-stop(17%, #ff0), color-stop(33%, #0f0), color-stop(50%, #0ff), color-stop(67%, #00f), color-stop(83%, #f0f), to(#f00));
    background: linear-gradient(to top, #f00 0%, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, #f00 100%);
}
.sketch-color-picker--c-hue--container {
    cursor: pointer;
    margin: 0 2px;
    position: relative;
    height: 100%;
}
.sketch-color-picker--c-hue--pointer {
    z-index: 2;
    position: absolute;
}
.sketch-color-picker--c-hue--picker {
    cursor: pointer;
    margin-top: 1px;
    width: 4px;
    border-radius: 1px;
    height: 8px;
    -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
    background: #fff;
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}



