
.sketch-color-picker--saturation, .sketch-color-picker--saturation---white, .sketch-color-picker--saturation---black {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.sketch-color-picker--saturation---white {
  background: -webkit-gradient(linear, left top, right top, from(#fff), to(rgba(255, 255, 255, 0)));
  background: linear-gradient(to right, #fff, rgba(255, 255, 255, 0));
}
.sketch-color-picker--saturation---black {
  background: -webkit-gradient(linear, left bottom, left top, from(#000), to(transparent));
  background: linear-gradient(to top, #000, transparent);
}
.sketch-color-picker--saturation---pointer {
  cursor: pointer;
  position: absolute;
}
.sketch-color-picker--saturation---circle {
  cursor: head;
  width: 4px;
  height: 4px;
  -webkit-box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3), 0 0 1px 2px rgba(0, 0, 0, 0.4);
          box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3), 0 0 1px 2px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  -webkit-transform: translate(-2px, -2px);
          transform: translate(-2px, -2px);
}



