
.sketch-color-picker {
  position: relative;
  width: 200px;
  margin: 0 auto;
  padding: 10px 10px 0;
  -webkit-box-sizing: initial;
          box-sizing: initial;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.15);
}
.sketch-color-picker.light {
    background: #ffffff;
}
.sketch-color-picker.light .sketch-color-picker--editable__label {
      color: #171d1f;
}
.sketch-color-picker.light .sketch-color-picker--presets-color {
      -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15);
              box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15);
}
.sketch-color-picker.dark {
    background: #171d1f;
}
.sketch-color-picker.dark .sketch-color-picker--editable__label {
      color: #f7f7f7;
}
.sketch-color-picker.dark .sketch-color-picker--presets {
      border-top-color: #1b2225;
}
.sketch-color-picker.dark .sketch-color-picker--presets-color {
      -webkit-box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.15);
              box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.15);
}
.sketch-color-picker--saturation-wrap {
    width: 100%;
    padding-bottom: 75%;
    position: relative;
    overflow: hidden;
}
.sketch-color-picker--controls {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.sketch-color-picker--sliders {
    padding: 4px 0;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.sketch-color-picker--sliders .sketch-color-picker--c-hue,
    .sketch-color-picker--sliders .sketch-color-picker--c-alpha--gradient {
      border-radius: 2px;
}
.sketch-color-picker--hue-wrap {
    position: relative;
    height: 10px;
}
.sketch-color-picker--alpha-wrap {
    position: relative;
    height: 10px;
    margin-top: 4px;
    overflow: hidden;
}
.sketch-color-picker--color-wrap {
    width: 24px;
    height: 24px;
    position: relative;
    margin-top: 4px;
    margin-left: 4px;
    border-radius: 3px;
}
.sketch-color-picker--active-color {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 4px rgba(0, 0, 0, 0.25);
            box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15), inset 0 0 4px rgba(0, 0, 0, 0.25);
    z-index: 2;
}
.sketch-color-picker--fields {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-top: 4px;
}
.sketch-color-picker--field-single {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding-left: 6px;
}
.sketch-color-picker--field-double {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.sketch-color-picker--presets {
    border-top: 1px solid #eee;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.sketch-color-picker--presets-color {
    border-radius: 3px;
    overflow: hidden;
    position: relative;
    display: inline-block;
    vertical-align: top;
    cursor: pointer;
    width: 16px;
    height: 16px;
    margin-bottom: 10px;
    margin-right: 10px;
    -webkit-box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15);
            box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.15);
}
.sketch-color-picker--presets-color:last-child {
      margin-right: auto;
}
.sketch-color-picker--presets-color:nth-child(8n) {
      margin-right: 0;
}
.sketch-color-picker--presets-color.add {
      margin-bottom: 10px !important;
}
.sketch-color-picker a.sketch-color-picker--presets-color {
    text-decoration: none;
    text-align: center;
    vertical-align: 0;
    line-height: 16px;
    margin: 0;
    font-size: 14px;
    font-weight: bold;
    color: #d9d9d9;
    border-radius: 2px;
    font-family: arial,sans-serif;
}



