
.sketch-color-picker--editable {
  position: relative;
}
.sketch-color-picker--editable__input {
    padding: 0;
    border: 0;
    outline: none;
    width: 80%;
    padding: 4px 10% 3px;
    border: 1px solid #ccc;
    font-size: 11px;
}
.sketch-color-picker--editable__input.nb-left {
      border-left: none;
}
.sketch-color-picker--editable__label {
    display: block;
    text-align: center;
    font-size: 11px;
    text-transform: capitalize;
    margin: 10px 0;
}



