
.sketch-color-picker--c-alpha {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
.sketch-color-picker--c-alpha-checkboard-wrap {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    overflow: hidden;
}
.sketch-color-picker--c-alpha-gradient {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
}
.sketch-color-picker--c-alpha-container {
    cursor: pointer;
    position: relative;
    z-index: 2;
    height: 100%;
    margin: 0 3px;
}
.sketch-color-picker--c-alpha-pointer {
    z-index: 2;
    position: absolute;
}
.sketch-color-picker--c-alpha-picker {
    cursor: pointer;
    width: 4px;
    border-radius: 1px;
    height: 8px;
    -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
            box-shadow: 0 0 2px rgba(0, 0, 0, 0.6);
    background: #fff;
    margin-top: 1px;
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}



